<template>
  <div class="bank-list">
    <van-list finished>
      <van-cell v-for="item in lists" :key="item.code" @click="selectedFn(item)">
        <img class="bank-logo" :src="imgBaseUrl + item.img + '.png'" alt="" slot="icon">
        <div class="bank-title" slot="title">{{item.name}}</div>
      </van-cell>
    </van-list>
  </div>
</template>

<script>
import { mapGetters } from 'vuex'

export default {
  name: 'bankslist',
  data() {
    return {
      imgBaseUrl: '//cdn09.ehaier.com/shunguang/H5/www/img/',
      lists: []
    }
  },
  computed: {
    ...mapGetters('freebankcard', ['getBankzcStyleInfo']),
  },
  mounted() {
    for (let key in this.getBankzcStyleInfo) {
      let json = {}
      json.code = key
      json.img = this.getBankzcStyleInfo[key].img
      json.name = this.getBankzcStyleInfo[key].name
      this.lists.push(json)
    }
    // var hash = {};
    // this.lists = this.lists.reduce((arr, item) => {
    //   if (!hash[item.name]) {
    //     hash[item.name] = true
    //     arr.push(item)
    //   }
    //   return arr
    // }, [])
  },
  methods: {
    selectedFn(item) {
      this.$emit('selected', item)
    }
  }
}
</script>

